import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import * as Editor from '@ckeditor/ckeditor5-build-classic';
import { takeUntil } from 'rxjs/internal/operators/takeUntil';
import { AppErrorHandler } from 'src/app/core/error-handler/app-error-handler.service';
import { PublisherAdminEditViewModel, PublisherAdminViewModel } from 'src/app/core/models/admin/PublisherViewModel';
import { NotificationService } from 'src/app/core/notification/notifications/customer/notification.service';
import { PublisherAdminService } from 'src/app/core/services/admin/publisher/publisher-admin.service';
import { DestroyService } from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import { SpinnerService } from 'src/app/core/services/comon-services/spinner/spinner.service';

@Component({
  selector: 'app-publisher-edit',
  templateUrl: './publisher-edit.component.html',
  styleUrls: ['./publisher-edit.component.less'],
  providers: [DestroyService]
})
export class PublisherEditComponent implements OnInit {
	isLoading = true; //skelton
	publisherId: number;
	result: PublisherAdminViewModel;
	uploading = false;
	public editor = Editor;
	publisherEditForm: FormGroup;
	ckEditor: string;
	modified = false;
	constructor(
		private route: ActivatedRoute,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService,
		private publisherAdminService: PublisherAdminService,
		private formBuilder: FormBuilder,
		private notificationService: NotificationService,
		private spinnerService: SpinnerService
	) {}

	ngOnInit(): void {
		if (this.route.snapshot.paramMap.get('publisherId')) {
			this.publisherId = +this.route.snapshot.paramMap.get('publisherId');
			if (this.publisherId) {
				console.log(this.publisherId);
				this.get(this.publisherId);
				this.initForm();
        this.checkFormValueChange();
				//attach data from store into formGroup
			}
		}
	}
	get(productId: number) {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.publisherAdminService
			.get(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.isSuccessed) {
						this.result = result.resultObj;
						this.initFormDataFromStore();
						console.log(this.result);
						this.isLoading = false;
					} else {
					}
				} else {
				}
			});
	}
	initForm() {
		this.publisherEditForm = this.formBuilder.group({
			name: ['', [Validators.required, Validators.minLength(3)]],
			description: ['', [Validators.required, Validators.minLength(3)]]
		});
	}
	initFormDataFromStore() {
		setTimeout(() => {
			this.publisherEditForm.setValue({
				name: this.result.publisherName != null ? this.result.publisherName : '',
				description: this.result.description != null ? this.result.description : ''
			});
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
		}, 5000);
	}
	checkFormValueChange() {
		this.publisherEditForm.valueChanges.subscribe((result) => {
			console.log('valueChanges');
			console.log(result);
			console.log(this.result);
			if (
				result.name === (this.result.publisherName != null ? this.result.publisherName : '') &&
				result.description === (this.result.description != null ? this.result.description : '')
			) {
				this.modified = false;
			} else {
				this.modified = true;
			}
			console.log(this.modified);
		});
	}
	getFormData() {
		const val = this.publisherEditForm.value;
		const publisherAdminEditViewModel: PublisherAdminEditViewModel = {
			publisherName: val.name,
			description: val.description,
			publisherId: this.result.publisherId
		};
		return publisherAdminEditViewModel;
	}
	submitForm(): void {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		try {
			this.publisherAdminService
				.edit(this.getFormData())
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(res) => {
						console.log(res);
						if (res.isSuccessed) {
							if (res.message) {
								this.notificationService.showSuccessNotification(res.message);
							}
						} else {
							if (res.message) {
								this.notificationService.showErrorNotification(res.message);
							}
						}
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					() => {
						this.notificationService.showErrorNotification('Có Lỗi Xảy Ra, Vui Lòng Thử Lại');
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					}
				);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
}
