import {PublisherCreateComponent} from './publisher-create/publisher-create.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {PublisherIndexComponent} from './publisher-index/publisher-index.component';
import {PublisherEditComponent} from './publisher-edit/publisher-edit.component';
const routes: Routes = [
	{path: 'create', component: PublisherCreateComponent},
	{path: '', component: PublisherIndexComponent},
	{path: 'edit/:publisherId', component: PublisherEditComponent}
];
@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class PublisherRoutingModule {}
