import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { takeUntil } from 'rxjs/internal/operators/takeUntil';
import { PublisherAdminViewModel } from 'src/app/core/models/admin/PublisherViewModel';
import { PagedResult } from 'src/app/core/models/common/page/PagedResult';
import { NotificationService } from 'src/app/core/notification/notifications/customer/notification.service';
import { PublisherAdminService } from 'src/app/core/services/admin/publisher/publisher-admin.service';
import { DestroyService } from 'src/app/core/services/comon-services/destroy-service/destroy.service';

@Component({
  selector: 'app-publisher-index',
  templateUrl: './publisher-index.component.html',
  styleUrls: ['./publisher-index.component.less'],
	providers: [DestroyService]
})
export class PublisherIndexComponent implements OnInit {
  pagingResult: PagedResult<PublisherAdminViewModel>;
	pageIndex: number = 1;
	searchKeyWord: string;
	isLoading = true;
	publisherSearchForm: FormGroup;
	constructor(
		private destroy$: DestroyService,
		private notificationService: NotificationService,
		private publisherAdminService: PublisherAdminService,
		private formBuilder: FormBuilder
	) {}

	ngOnInit(): void {
		this.get();
		this.initSearchForm();
		this.checkSearchFormValueChange();
	}
	initSearchForm() {
		this.publisherSearchForm = this.formBuilder.group({
			search: ['', [Validators.required, Validators.minLength(1)]]
		});
	}
	get() {
		this.publisherAdminService
			.getPaging({
				pageIndex: this.pageIndex,
				pageSize: 10,
				searchKeyWord: this.searchKeyWord ?? '',
				languageCode: 'vn'
			})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.pagingResult = result.resultObj;
							console.log(this.pagingResult);
							this.isLoading = false;
						} else {
						}
					} else {
					}
				} else {
				}
			});
	}
	pageChanged(page: number) {
		this.pageIndex = page; // receive the page number
		// display the sort button by previous sort
		this.get(); // navigated by sort and page
	}
	changeStatus(status: boolean, publisherId: number) {
		console.log(status);
		this.publisherAdminService
			.changeStatus({publisherId: publisherId, status: status})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.notificationService.showSuccessNotification(result.message);
						} else {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				}
			});
	}
	checkSearchFormValueChange() {
		this.publisherSearchForm.valueChanges.subscribe((result) => {
			console.log(result.search);
			this.searchKeyWord = result.search;
			this.pageIndex = 1;
			this.get();
		});
	}
	deletepublisher(publisherId: number) {
		console.log(publisherId);
		this.publisherAdminService
			.delete(publisherId)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				console.log(result);
				if (result) {
					if (result.resultObj) {
						if (result.isSuccessed) {
							this.notificationService.showSuccessNotification('Xóa Thành Công');
						} else {
						}
					} else {
						this.notificationService.showSuccessNotification(result.message);
					}
				} else {
				}
			});
	}
  cancel(){}
}
