import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { PublisherAdminCreateViewModel, PublisherAdminEditViewModel, PublisherAdminViewModel } from 'src/app/core/models/admin/PublisherViewModel';
import { ApiResult } from 'src/app/core/models/common/ApiResult';
import { PagedResult } from 'src/app/core/models/common/page/PagedResult';
import { PagingRequestBase } from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import { AdminUrlPublisher } from 'src/app/core/utils/URL/admin-url/admin-url-publisher';

@Injectable({
  providedIn: 'root'
})
export class PublisherAdminService {

  constructor(private adminUrlPublisher: AdminUrlPublisher, private http: HttpClient) {}
	create(publisherAdminCreateViewModel: PublisherAdminCreateViewModel):Observable<ApiResult<boolean>> {
		return this.http.post(this.adminUrlPublisher.create(), publisherAdminCreateViewModel);
	}
	getPaging(pagingRequestBase: PagingRequestBase): Observable<ApiResult<PagedResult<PublisherAdminViewModel>>> {
		return this.http.get(this.adminUrlPublisher.getPaging(pagingRequestBase));
	}
	get(id: number): Observable<ApiResult<PublisherAdminViewModel>> {
		return this.http.get<ApiResult<PublisherAdminViewModel>>(this.adminUrlPublisher.get(id));
	}
	delete(id: number): Observable<ApiResult<boolean>> {
		return this.http.delete(this.adminUrlPublisher.delete(id));
	}
	edit(publisherAdminEditViewModel: PublisherAdminEditViewModel): Observable<ApiResult<boolean>> {
		return this.http.put(this.adminUrlPublisher.edit(), publisherAdminEditViewModel);
	}
	changeStatus({publisherId:number, status:boolean}): Observable<ApiResult<boolean>> {
		return this.http.put(this.adminUrlPublisher.changeStatus(), {publisherId:number, status:boolean});
	}
}
